
















































/*-----------------------------------  
App section
-----------------------------------*/
.app-section[data-v-edf95e9a] {
    padding-top: 35px;
    position: relative
}
.app-section[data-v-edf95e9a]:after {
    content: "";
    background: url(../../assets/img/how-get-apps/city-2.png) center bottom;
    background-color: #ffb42d;
    display: block;
    position: absolute;
    top: 100px;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background-repeat: repeat-x;
}
.app-wrap[data-v-edf95e9a] {
    position: relative;
    z-index: 2
}
@media (min-width: 280px) and (max-width: 568px) {
.app-wrap[data-v-edf95e9a] {
        padding-bottom: 30px
}
}
.app-wrap figure[data-v-edf95e9a] {
    margin: 0;
    text-align: center;
}
.app-wrap .img-fluid[data-v-edf95e9a] {
    height: 339px;
    width: 233px;
}
.app-wrap .left-text[data-v-edf95e9a] {
    padding-top: 110px;
}
.app-wrap .left-text h3[data-v-edf95e9a],
.app-wrap .left-text p[data-v-edf95e9a] {
    color: #fff;
}


/* ==========================
*  Download app buttons
============================*/
.app-btn[data-v-edf95e9a] {
    color: #FFFFFF;
    float: left;
    margin-right: 15px;
    background: #81858b;
    margin-top: 5px;
    border-radius: 2px;
    -webkit-transition: all .3s;
    transition: all .3s;
}
.app-btn i[data-v-edf95e9a] {
    font-size: 38px;
    line-height: 50px;
    margin: 0px;
    padding: 0 18px;
}
.app-btn .pull-right[data-v-edf95e9a] {
    padding: 8px 25px 8px 10px;
}
.app-btn .text[data-v-edf95e9a] {
    display: block;
    font-size: 12px;
    line-height: 1;
}
.app-btn[data-v-edf95e9a]:hover,
.app-btn[data-v-edf95e9a]:active,
.app-btn[data-v-edf95e9a]:focus {
    color: #FFFFFF;
    background: #111111;
}
.android-button[data-v-edf95e9a] {
    background: #82b21c;
}
@media (min-width: 348px) and (max-width: 767px) {
.app-section[data-v-edf95e9a] {
        padding: 55px 0;
}
.text-img-block figure[data-v-edf95e9a] {
        display: none
}
}
@media (min-width: 768px) and (max-width: 991px) {
.app-wrap .left-text[data-v-edf95e9a] {
        padding-top: 100px;
        font-size: 80%;
}
.app-btn i[data-v-edf95e9a] {
        font-size: 28px;
        line-height: 41px;
        margin: 0px;
        padding: 0 18px;
}
.app-btn[data-v-edf95e9a] {
        padding-right: 5px
}
.app-btn .pull-right[data-v-edf95e9a] {
        padding: 7px 14px 0 5px
}
.app-btn i[data-v-edf95e9a] {
        padding: 0 10px;
}
}



